.container {
    min-height: 100vh;
    width: 100%;

    display: grid;
    grid-template-rows: minmax(min-content, 6rem) 1fr minmax(min-content, 3rem);

    // background: linear-gradient(to bottom right, rgb(12, 90, 60), rgb(50, 12, 170));
    background: linear-gradient(to left bottom, rgb(50, 90, 152), rgb(4, 126, 55));
    color: white;
}

.section-main {
    max-width: 120rem;
    min-width: 60rem;
    margin: 0 auto;
}

%heading {
    font-weight: 300;
    text-align: center;
    margin-bottom: 2rem;
}

.heading {
    &-1 {
        @extend %heading;
        font-size: 4rem;
    }

    &-2 {
        @extend %heading;
        font-size: 3rem;
    }
}
